//header
.header {
    box-sizing: border-box;
    grid-area: h;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    z-index: 2;
    background: #ffffff;
    height: 110px;
    position: sticky;
    top: -10px;
    border-radius: 0 0 4px 4px;
    box-shadow: rgba(17, 12, 46, 0.16) 0px 3px 8px;
    &__wrap {
        margin: 0 auto;
        text-align: center;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 0 2px; } }

.HeaderMenu {
    display: flex;
    align-items: center;
    margin-left: auto; }







