.header {
    &__img {
        display: block;
        border-radius: 8px;
        padding: 4px;
        margin-left: -2px;
        width: 80px;
        height: 80px;
        box-shadow: rgba(0, 176, 155, .4) 5px 5px, rgba(0, 176, 155, 0.3) 10px 10px, rgba(0, 176, 155, 0.2) 15px 15px, rgba(0, 176, 155, 0.1) 20px 20px, rgba(0, 176, 155, 0.05) 25px 25px;
        &__item {
            width: 80px;
            height: 80px; } }

    &__menu {
        display: flex;
        align-items: center;
        padding: 10px 24px 10px 10px;
        border-radius: 4px;

        &__separ {
            display: inline-block;
            width: 1.7px;
            height: 36px;
            background: #1f1f1f;
            margin: 0 11px; }

        &__list {
            gap: 10px;
            display: flex;
            align-items: center;

            &__item {
                list-style: none;
                position: relative;

                &__link {
                    display: flex;
                    align-items: center;
                    text-decoration: none;
                    letter-spacing: 1px;
                    font-family: 'Roboto', sans-serif;
                    color: #1f1f1f;
                    font-weight: 7n00;
                    font-size: 19px;
                    cursor: pointer;
                    gap: 10px;
                    transition: ease-in .2s;
                    &__fq {
                        font-family: 'Yomogi', cursive;
                        font-weight: 600;
                        color: #1f1f1f;
                        text-decoration: none;
                        font-size: 17px; }
                    &__avatar {
                        border-radius: 50%;
                        display: block;
                        width: 50px;
                        height: 50px;
                        max-width: 50px;
                        min-width: 50px;
                        max-height: 50px;
                        min-height: 50px;

                        &__item {
                            box-sizing: border-box;
                            border-radius: 50%;
                            width: 50px;
                            height: 50px;
                            max-width: 50px;
                            min-width: 50px;
                            max-height: 50px;
                            min-height: 50px;
                            object-fit: cover; } }
                    &__text {
                        display: flex;
                        align-items: center;
                        transition: ease-in .2s;
                        text-decoration: none; } } } } } }


