.auth__wrap {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    display: flex;
    flex-direction: column;
    width: 100px;
    height: 50px;
    background: #f3f3f3;
    border-radius: 3px;
    position: absolute;
    top: 55px;
    right: 0;
    left: 0;
    margin-left: auto;
    margin-right: -16px;
    padding: 10px;
    &__active {}
    &__link {
        position: relative;
        color: #000000;
        text-align: left;
        font-size: 15px;
        font-weight: 400;
        margin-bottom: 10px;
        text-decoration: none;
        &:last-child {
            margin-bottom: 0; } }

    &__arrow {

        &:after {
            content: '';
            position: absolute;
            right: 16px;
            top: -18px;
            border: 7px solid transparent;
            border-bottom: 11px solid #f3f3f3; } } }


.arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 14px;
    height: 14px;
    transform: rotate(-45deg);
    margin-bottom: 3px;
    &:before,
    &:after {
        content: '';
        position: absolute;
        box-sizing: border-box;
        width: 57%;
        height: 57%;
        border-left: 1px solid #1f1f1f;
        border-bottom: 1px solid #1f1f1f; }
